import React, { useState } from 'react'
import styled from 'styled-components'
import { motion } from 'framer-motion'

const variants = {
  left: {
    x: -150,
    borderRadius: 8,
  },
  right: {
    x: 150,
    borderRadius: 128,
  },
}

function App() {
  const [animate, setAnimate] = useState(true)
  return (
      <Main className="w-full h-screen flex float-left">
        <div className="w-1/3 p-1 items-center justify-center">
          <textarea
              placeholder="Enter your code here"
              className="shadow appearance-none border rounded w-full text-gray-700 focus:outline-none focus:shadow-outline"
              style={{height: "85vh"}}
              id="program_code" />
              <div className="sm:w-full h-10 p-4 grid-cols-3 grid-flow-col gap-4 float-left items-center justify-center"
                   style={{height: "10vh"}}>
                <button className="text-base w-1/4 ml-4 bg-blue-300 hover:bg-blue-400 p-4 font-bold text-white" id="compile">
                  COMPILE
                </button>
                <button className="text-base w-1/4 ml-4 bg-blue-300 hover:bg-blue-400 p-4 font-bold text-white" id="preview">
                  PREVIEW
                </button>
                <button className="text-base w-1/4 ml-4 bg-blue-300 hover:bg-blue-400 p-4 font-bold text-white" id="export">
                  EXPORT
                </button>
              </div>
        </div>
        <div className="h-screen w-2/4 p-1 items-center justify-center">
          <motion.div
              variants={variants}
              animate={animate ? 'left' : 'right'}
              className="w-32 h-32 egg bg-blue-600 shadow-lg "
          />
          <section className="mt-16">
            <button
                className="text-base bg-gray-200 rounded-md text-black px-4 py-2 font-semibold"
                onClick={() => setAnimate((prev) => !prev)}
            >
              Click me!
            </button>
          </section>
        </div>
      </Main>
      /*
    <Main className="w-full flex flex-col items-center justify-center">
      <motion.div
        variants={variants}
        animate={animate ? 'left' : 'right'}
        className="w-32 h-32 bg-blue-600 shadow-lg"
      ></motion.div>
      <section className="mt-16">
        <button
          className="text-base bg-gray-200 rounded-md text-black px-4 py-2 font-semibold"
          onClick={() => setAnimate((prev) => !prev)}
        >
          Click me!
        </button>
      </section>
    </Main>

       */
  )
}

export default App

const Main = styled.main`
  height: 100vh;
`
